/*  Overview Hero Video Banner
*
*  Version:
*    1.0.0 - 2022/01/14
*/

@import '../base.scss';

.c-overview-hero {
  position: relative;
  padding-top: 8rem;
  padding-bottom: 3.8125rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 33rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__heading {
    @include t-gutters;
    text-align: center;
    color: $s-color--font-6;
    margin-bottom: 2.25rem;
  }

  &__challenge-weeks {
    margin-left: 2.5rem;
  }

  &__progress {
    &-container {
      padding-left: 2.625rem;
      position: relative;
      overflow-x: auto;
      ms-overflow-style: none; // IE and Edge
      scrollbar-width: none; // Firefox
      height: 4.0625rem;
      text-align: center;

      &::-webkit-scrollbar {
        display: none; // Chrome, Safari, Opera
      }

      .is-week-1 {}
    }

    &-items {
      display: inline-flex;
      position: relative;
      margin-right: 2.5rem;
      --progress: 0;

      &::before {
        content: '';
        position: absolute;
        background: linear-gradient(to right, #004851 var(--progress), white var(--progress));
        top: 1.1875rem;
        height: 0.25rem;
        width: 100%;
        transition: 0.4s ease;
      }
    }

    &-item {
      position: relative;
      // background-color: $s-color--background-secondary;
      border-radius: 50%;
      height: 2.5rem;
      width: 2.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 0.4s ease;
      flex-shrink: 0;

      &:not(:first-child) {
        margin-left: 5.5rem;
      }

      // &.is-active~.c-overview-hero__progress-item {
      //   background-color: white;

      //   .c-overview-hero__progress-check {
      //     display: none;
      //   }

      //   .c-overview-hero__progress-title {
      //     color: white;
      //   }

      //   .c-overview-hero__progress-lock {
      //     display: block;
      //   }
      // }
     
      &.is-locked {
        background-color: white;

        &.is-active{
          background-color: $s-color--background-secondary;
          
          .c-overview-hero__progress-lock {
            background-color: $s-color--background-secondary;
            path{
              fill: #fff;
            }
          }

          .c-overview-hero__progress-title {
            color: $s-color--background-secondary;
          }
        }

        .c-overview-hero__progress-check {
          display: none;
        }

        .c-overview-hero__progress-title {
          color: white;
        }

        .c-overview-hero__progress-lock {
          display: block;
        }
      }
      &.is-unlocked{
        border: 1px solid $s-color--background-secondary;

        &.is-active{
          background-color: $s-color--background-secondary;
        }

        &:not(.is-active) {
          background-color: $s-color--background-tertiary;
          path{
            stroke: $s-color--background-secondary;
          }
        }

        .c-overview-hero__progress-check {
          display: block;
        }

        .c-overview-hero__progress-title {
          color: $s-color--font-1;
        }

        .c-overview-hero__progress-lock {
          display: none;
        }
      }
    



    }

    &-check {
      // display: block;
      height: 1.125rem;
      width: 1.125rem;
    }

    &-lock {
      height: 1.125rem;
      width: 1.125rem;
      // display: none;
      background-color: white;
    }

    &-title {
      position: absolute;
      white-space: nowrap;
      bottom: -1.5rem;
      transform: translateX(-50%);
      left: 50%;
    }
  }

  &__wrapper {
    @include t-gutters;
    position: relative;
    margin-top: 4.25rem;
    cursor: pointer;
    overflow: hidden;
    display: block;

    &>* {
      pointer-events: none;
    }

    &:hover {
      .c-overview-hero__play-button {
        transform: scale(1);
      }
    }
  }

  &__image {
    &-container {
      position: relative;
      padding-bottom: 148%;
    }

    &.is-desktop {
      display: none;

      .has-desktop-only & {
        display: block;
      }
    }
  }

  &__content {
    position: absolute;
    @include t-absolute-center-x-y();
    color: $s-color--font-6;
    width: 80%;
    text-align: center;

    &-pretitle {
      color: $s-color--font-6;
    }

    &-title {
      margin-top: 1.25rem;
    }
  }

  &__play-button {
    @include t-width-and-height(73);
    display: block;
    margin: 1.875rem auto 0;
    transform: scale(.9);
    transition: transform $s-anim--time-short $s-anim--ease-in-out;

    .icon--play {
      &-inner {
        fill: $s-color--background-tertiary;
      }
    }
  }

  &__teaser {
    &-wrapper {
      padding-left: 1rem;
      padding-right: 1rem;
      position: relative;
      margin-top: 4.25rem;
      cursor: pointer;
      overflow: hidden;
      display: block;

      &>* {
        pointer-events: none;
      }

      &:hover {
        .c-overview-hero__teaser-play-button {
          transform: scale(1);
        }
      }
    }

    &-image {
      &-container {
        position: relative;
        padding-bottom: 148%;
      }

      &.is-desktop {
        display: none;

        .has-desktop-only & {
          display: block;
        }
      }
    }

    &-image-overlay {
      @include t-absolute-center-x-y();
      background: rgba(255, 255, 255, 0.2);
      backdrop-filter: blur(4.625rem);
      width: 80%;
      height: 84%;
    }

    &-content {
      position: absolute;
      @include t-absolute-center-x-y();
      color: $s-color--font-6;
      width: 80%;
      text-align: center;

      &-pretitle {
        color: $s-color--font-6;
      }

      &-title {
        margin-top: 1.25rem;
      }
    }

    &-play-button {
      @include t-width-and-height(73);
      display: block;
      margin: 1.875rem auto 0;
      transform: scale(.9);
      transition: transform $s-anim--time-short $s-anim--ease-in-out;

      .icon--play {
        &-inner {
          fill: $s-color--background-tertiary;
        }
      }
    }

    &-info {
      @include t-gutters;
      margin-top: 47px;

      &-content {
        margin-top: 34px;
        margin-bottom: 55px;
      }

      &-pretitle {
        text-transform: uppercase;
      }

      &-title {
        margin-top: 20px;
        text-transform: capitalize;
      }

      &-text {
        margin-top: 21px;

        p {
          line-height: 18px;

          &:not(:last-child) {
            margin-bottom: 19px;
          }
        }
      }
    }

    &-image1of2-container {
      padding-bottom: 132%;
    }

    &-image2of2-container {
      padding-bottom: 85%;
    }
    &-modal{
      padding: 2rem;
    }
  }

  @media screen and (min-width: 550px) {
    &__teaser {
      &-wrapper {
        @include t-gutters;
      }

      &-image-container {
        padding-bottom: 90%;
      }

      &-image1of2-container {
        padding-bottom: 65%;
      }

      &-image2of2-container {
        padding-bottom: 60%;
      }
    }
  }

  @include t-media-query($s-small-up) {

    &__image-container {
      padding-bottom: 80%;
    }

    &__image {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }
    }
    &__teaser {
      &-image-container {
        padding-bottom: 55%;
      }
  
      &-image {
        &.is-mobile {
          display: none;
        }
  
        &.is-desktop {
          display: block;
        }
      }
  
      &-info {
        margin-top: 150px;
        display: flex;
        justify-content: space-between;
  
        &-content {
          width: 40%;
          margin-top: 40px;
        }
      }
  
      &-image1of2-container {
        width: 25%;
        padding-bottom: 35%;
      }
  
      &-image2of2-container {
        margin-top: 50px;
        width: 25%;
        padding-bottom: 30%;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__heading {
      margin-top: 8.5625rem;
      margin-bottom: 3.625rem;
    }

    &__header-background {
      padding-bottom: 53rem;
    }

    &__image-container {
      padding-bottom: 45%;
    }

    &__content-title {
      margin-top: 1.125rem;
    }

    &__play-button {
      @include t-width-and-height(100);
      margin-top: 3.625rem;
    }

    &__progress {
      &-container {
        margin-bottom: 8.8125rem;
        height: 4.6875rem;
      }

      &-items {

        &::before {
          top: 1.4375rem;
        }
      }

      &-item {
        height: 3rem;
        width: 3rem;

        &:not(:first-child) {
          margin-left: 9rem;
        }
      }

      &-check {
        height: 1.375rem;
        width: 1.375rem;
      }

      &-lock {
        height: 1.375rem;
        width: 1.375rem;
      }

      &-title {
        bottom: -1.6875rem;
      }
    }

    &__teaser {
      &-image-container {
        padding-bottom: 46%;
      }

      &-image-overlay {
        width: 94%;
        height: 89%;
      }

      &-content-title {
        margin-top: 1.125rem;
      }

      &-play-button {
        @include t-width-and-height(100);
        margin-top: 3.625rem;
      }

      &-info {
        margin-top: 162px;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__teaser {
      &-image1of2-container {
        width: 26%;
        margin-bottom: 107px;
      }

      &-image2of2-container {
        margin-top: 190px;
        width: 26%;
        padding-bottom: 30%;
      }

      &-info {
        &-content {
          margin-left: 116px;
          width: 27%;
          margin-top: 148px
        }

        &-title {
          margin-top: 22px;
        }

        &-text {
          margin-top: 39px;
          max-width: 427px;

          p {
            line-height: 25px;

            &:not(:last-child) {
              margin-bottom: 19px;
            }
          }
        }
      }
    }
  }
}